import Layout from '../common/Layout';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import firebase from '../firebase';
import styled from 'styled-components';

const BtnSet = styled.div`
	display: flex;
	gap: 20px;
	margin-top: 20px;
	padding-bottom: 60px;
`;
const Form = styled.div`
	background-color: white;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding-top: 60px;
	border-radius: 10px;
	margin-bottom: auto;
	box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.2);
`;

const Input = styled.div`
	display: flex;
	align-items: center;
	justify-content: center;
	width: 60%;
	height: 40px;
	margin-bottom: 40px;

	label {
		height: 40px;
		width: 20%;
		margin-right: 20px;
		> h2 {
			font-size: 15px;
			display: flex;
			line-height: 1.8;
		}
	}
	input {
		width: 70%;
		height: 40px;

		border-radius: 10px;
	}
`;

function Loign() {
	const navigate = useNavigate();
	const [Email, setEmail] = useState('');
	const [Pwd, setPwd] = useState('');
	const [Err, setErr] = useState('');

	const handleLogin = async () => {
		if (!(Email && Pwd)) return alert('  ');

		try {
			await firebase.auth().signInWithEmailAndPassword(Email, Pwd);
			navigate('/');
		} catch (err) {
			if (err.code === 'auth/user-not-found') setErr('  .');
			else if (err.code === 'auth/wrong-password') setErr('   .');
			else setErr(' ');
		}
	};

	return (
		<Layout name={'Login'}>
			<Form>
				<Input>
					<label htmlFor='Email'></label>
					<input type='email' value={Email} placeholder='  .' onChange={(e) => setEmail(e.target.value)} />
				</Input>
				<Input>
					<label htmlFor='Pwd'></label>

					<input type='password' value={Pwd} placeholder=' .' onChange={(e) => setPwd(e.target.value)} />
				</Input>
				<BtnSet>
					<button onClick={handleLogin}></button>
					<button onClick={() => navigate('/join')}></button>
				</BtnSet>
				{Err !== '' && <p>{Err}</p>}
			</Form>
		</Layout>
	);
}

export default Loign;
