import Layout from '../common/Layout';
import { useParams, Link, useNavigate } from 'react-router-dom';
import axios from 'axios';
import { useEffect, useState } from 'react';
import styled from 'styled-components';
import { useSelector } from 'react-redux';

const DetailWrap = styled.div`
	width: 100%;
	padding: 40px;
	background: #fff;
	box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.02);
	border-radius: 10px;
	box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.2);
`;

const BtnSet = styled.nav`
	display: flex;
	gap: 20px;
	margin-top: 20px;
`;

function Detail() {
	const user = useSelector((store) => store.user);
	const params = useParams();
	const navigate = useNavigate();
	const [Detail, setDetail] = useState(null);

	const handleDelete = () => {
		if (!window.confirm(' ?')) return;

		axios
			.delete(`/api/community/delete/${params.num}`)
			.then((res) => {
				if (res.data.success) {
					alert(' .');
					navigate('/list');
				} else {
					alert('  .');
				}
			})
			.catch((err) => console.log(err));
	};

	useEffect(() => {
		axios
			.get(`/api/community/detail/${params.num}`)
			.then((res) => {
				if (res.data.success) {
					setDetail(res.data.detail);
				} else {
					alert('  .');
				}
			})
			.catch((err) => console.log(err));
	}, [params]);

	return (
		<Layout name={'Detail'}>
			{
				<>
					<DetailWrap>
						<h2>{Detail?.title}</h2>
						<p>{Detail?.content}</p>
						<span>: {Detail?.writer.displayName}</span>
						{Detail?.createdAt === Detail?.updatedAt ? (
							<p>: {Detail?.createdAt.split('T')[0]}</p>
						) : (
							<p>: {Detail?.updatedAt.split('T')[0]}</p>
						)}
					</DetailWrap>

					{/*         ,   */}
					{user.uid === Detail?.writer.uid && (
						<BtnSet>
							<button>
								<Link to={`/edit/${params.num}`}>Edit</Link>
							</button>
							<button onClick={handleDelete}>Delete</button>
						</BtnSet>
					)}
				</>
			}
		</Layout>
	);
}

export default Detail;
